'use client';

import { useState } from 'react';

const LoginForm = ({ selectedRole, onLogin, onBack }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [error, setError] = useState('');

  // Demo user credentials for each role
  const demoUsers = {
    member: [
      { username: 'john.smith', password: 'member123', name: 'John Smith' },
      { username: 'sarah.jones', password: 'member456', name: 'Sarah Jones' },
      { username: 'demo', password: 'demo', name: 'Demo Member' }
    ],
    advisor: [
      { username: 'advisor.mike', password: 'advisor123', name: 'Mike Wilson' },
      { username: 'advisor.lisa', password: 'advisor456', name: 'Lisa Chen' },
      { username: 'demo', password: 'demo', name: 'Demo Advisor' }
    ],
    regulator: [
      { username: 'reg.admin', password: 'regulator123', name: 'Admin Regulator' },
      { username: 'compliance.officer', password: 'regulator456', name: 'Compliance Officer' },
      { username: 'demo', password: 'demo', name: 'Demo Regulator' }
    ]
  };

  const getRoleConfig = (role) => {
    const configs = {
      member: {
        title: 'Member Portal',
        color: 'blue',
        bgColor: 'bg-blue-600',
        hoverColor: 'hover:bg-blue-700',
        focusColor: 'focus:ring-blue-200'
      },
      advisor: {
        title: 'Advisor Portal',
        color: 'green',
        bgColor: 'bg-green-600',
        hoverColor: 'hover:bg-green-700',
        focusColor: 'focus:ring-green-200'
      },
      regulator: {
        title: 'Regulator Portal',
        color: 'red',
        bgColor: 'bg-red-600',
        hoverColor: 'hover:bg-red-700',
        focusColor: 'focus:ring-red-200'
      }
    };
    return configs[role];
  };

  const roleConfig = getRoleConfig(selectedRole);
  const users = demoUsers[selectedRole];

  const handleSubmit = (e) => {
    e.preventDefault();
    
    // Check credentials against demo users
    const user = users.find(u => u.username === username && u.password === password);
    
    if (user) {
      // Successful login
      onLogin({
        role: selectedRole,
        username: username,
        name: user.name
      });
    } else {
      // Failed login
      setError('Invalid username or password. Please try again.');
      
      // Hide error after 3 seconds
      setTimeout(() => {
        setError('');
      }, 3000);
    }
  };

  const togglePassword = () => {
    setShowPassword(!showPassword);
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-50 to-indigo-100 relative">
      <div className="bg-white rounded-2xl shadow-xl p-8 w-full max-w-md">
        <button 
          onClick={onBack}
          className="absolute top-4 left-4 p-2 text-gray-500 hover:text-gray-700 rounded-lg hover:bg-gray-100"
        >
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 19l-7-7 7-7"/>
          </svg>
        </button>

        <div className="text-center mb-8">
          <div className={`w-16 h-16 ${roleConfig.bgColor} rounded-xl flex items-center justify-center mx-auto mb-4`}>
            <svg className="w-8 h-8 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z"/>
            </svg>
          </div>
          <h1 className="text-2xl font-bold text-gray-900">Sign In</h1>
          <p className="text-gray-600 mt-2">{roleConfig.title}</p>
        </div>
        
        <form onSubmit={handleSubmit} className="space-y-6">
          {error && (
            <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-lg text-sm">
              {error}
            </div>
          )}
          
          <div>
            <label htmlFor="username" className="block text-sm font-medium text-gray-700 mb-2">
              Username
            </label>
            <input 
              type="text" 
              id="username" 
              name="username" 
              required
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              className="w-full border border-gray-300 rounded-lg px-4 py-3 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none transition-colors"
              placeholder="Enter your username"
            />
          </div>
          
          <div>
            <label htmlFor="password" className="block text-sm font-medium text-gray-700 mb-2">
              Password
            </label>
            <div className="relative">
              <input 
                type={showPassword ? "text" : "password"}
                id="password" 
                name="password" 
                required
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="w-full border border-gray-300 rounded-lg px-4 py-3 pr-12 focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none transition-colors"
                placeholder="Enter your password"
              />
              <button
                type="button"
                onClick={togglePassword}
                className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-500 hover:text-gray-700"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  {showPassword ? (
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M13.875 18.825A10.05 10.05 0 0112 19c-4.478 0-8.268-2.943-9.543-7a9.97 9.97 0 011.563-3.029m5.858.908a3 3 0 114.243 4.243M9.878 9.878l4.242 4.242M9.878 9.878L8.464 8.464M9.878 9.878l-1.415-1.414M14.12 14.12l1.415 1.415M14.12 14.12L15.535 15.535M14.12 14.12l1.414 1.414"/>
                  ) : (
                    <>
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"/>
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z"/>
                    </>
                  )}
                </svg>
              </button>
            </div>
          </div>
          
          <div className="flex items-center justify-between">
            <label className="flex items-center">
              <input type="checkbox" className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"/>
              <span className="ml-2 text-sm text-gray-600">Remember me</span>
            </label>
            <a href="#" className="text-sm text-blue-600 hover:text-blue-700">Forgot password?</a>
          </div>
          
          <button 
            type="submit"
            className={`w-full ${roleConfig.bgColor} text-white py-3 rounded-lg ${roleConfig.hoverColor} focus:ring-4 ${roleConfig.focusColor} transition-all font-medium`}
          >
            Sign In
          </button>
        </form>
        
        {/* Demo Credentials */}
        <div className="mt-8 p-4 bg-gray-50 rounded-lg">
          <h4 className="text-sm font-medium text-gray-900 mb-2">Demo Credentials:</h4>
          <div className="text-xs text-gray-600 space-y-1">
            {users.map((user, index) => (
              <div key={index}>
                <strong>{user.username}</strong> / {user.password}
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginForm;
